.generation {
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 90px 0;
  }

  &__container {
    display: flex;
    flex-direction: column;

    @media screen and (max-width: 600px){
      margin: 0 20px;
      padding: 30px 50px;
    }
    @media screen and (min-width: 600px){
      padding: 80px 165px;
    }
    border-radius: 20px;
    text-align: center;

    background-color: #fff;
  }

  &__main {
    display: flex;
    flex-direction: column;

    h1 {
      font-weight: 500;
      line-height: 38px;

      letter-spacing: 0.19em;
      text-transform: uppercase;

      color: #494949;
    }
    h3 {
      font-weight: 400;
      width: 508px;
      font-size: 24px;
      line-height: 29px;
      text-align: center;
      letter-spacing: 0.1em;

      color: #606060;
    }

    @media screen and (max-width: 600px){
      gap: 30px;

      h1 {
        font-size: 16px;
      }
    }
    @media screen and (min-width: 600px){
      gap: 27px;

      h1 {
        font-size: 22px;
      }
    }

    &__input {
      display: flex;
      align-items: center;
      flex-direction: column;
      margin-top: 20px;
      margin-left: 0;
      margin-right: auto;

      input {
        max-width: 390px;
        width: 100%;
        height: 85px;
        background: #F7F7F7;
        border: 1px solid #C7C7C7;
        border-radius: 20px;
        padding: 12px;
      }
    }
  }
}

